:root {
  --ugaam-close-button-color: #A4A4A4;
  --ugaam-close-button-background: #FFFFFF;
  --ugaam-close-button-hover-color: #888888;
  --ugaam-close-button-hover-background: #DDDDDD;
}

.ugaam-ad-wrapper-outer {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  max-width: 100%;
  /**
  *
  * Visibility of ads.
  *
  * @since 1.2.0
  */
  /**
  *
  * Alignment of ads.
  *
  * @since 1.2.0
  */
}
.ugaam-ad-wrapper-outer a {
  position: relative;
  display: inline-flex;
  width: fit-content;
  max-width: 100%;
  z-index: 5;
  overflow: hidden;
}
.ugaam-ad-wrapper-outer iframe {
  position: relative;
  display: inline-flex;
  width: fit-content;
  max-width: 100%;
  z-index: 5;
}
.ugaam-ad-wrapper-outer img {
  position: relative;
  display: inline-flex;
  width: fit-content;
  max-width: 100%;
  z-index: 5;
}
.ugaam-ad-wrapper-outer .ugaam-ad-wrapper-inner {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  text-align: left;
  justify-content: flex-start;
}
.ugaam-ad-wrapper-outer .ugaam-ad-wrapper-inner :is(.ugaam-custom-ad, .ugaam-adsense-ad, .ugaam-ad-content) {
  position: relative;
  width: 100%;
  max-width: 100%;
  height: 100%;
}
.ugaam-ad-wrapper-outer .ugaam-ad-wrapper-inner .ugaam-custom-ad {
  display: flex;
  flex-direction: column-reverse;
  align-items: center;
}
.ugaam-ad-wrapper-outer[data_display-ad=false] {
  opacity: 0;
  visibility: hidden;
  backface-visibility: hidden;
}
.ugaam-ad-wrapper-outer[data_display-ad=true] {
  opacity: 1;
  visibility: visible;
  backface-visibility: visible;
}
.ugaam-ad-wrapper-outer:is(.is-left-align) {
  display: grid;
  justify-content: start;
  width: 100%;
}
.ugaam-ad-wrapper-outer:is(.is-left-align) .ugaam-ad-wrapper-inner {
  justify-content: flex-start;
  text-align: left;
}
.ugaam-ad-wrapper-outer:is(.is-right-align) {
  display: grid;
  justify-content: end;
  width: 100%;
}
.ugaam-ad-wrapper-outer:is(.is-right-align) .ugaam-ad-wrapper-inner {
  justify-content: flex-end;
  text-align: right;
}
.ugaam-ad-wrapper-outer:is(.is-center-align) {
  display: grid;
  grid-template-columns: 1fr;
  justify-content: center;
}
.ugaam-ad-wrapper-outer:is(.is-center-align) .ugaam-ad-wrapper-inner {
  justify-content: center;
  text-align: center;
}

.ugaam-ad-wrapper-outer :is(button.ugaam-ad-close-button) {
  position: relative;
  z-index: 2;
  display: none;
  align-items: center;
  justify-content: center;
  line-height: 1;
  border: none;
  background: transparent;
  cursor: pointer;
  line-height: 1;
  height: 30px;
  width: 80px;
  fill: var(--ugaam-close-button-color);
  color: var(--ugaam-close-button-color);
  background-color: var(--ugaam-close-button-background);
  border-radius: 0;
  box-shadow: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  opacity: 0;
  visibility: hidden;
  backface-visibility: hidden;
  transition: all 0.5s ease;
}
.ugaam-ad-wrapper-outer :is(button.ugaam-ad-close-button):is(:hover, :focus) {
  text-decoration: none;
  fill: var(--ugaam-close-button-hover-color);
  color: var(--ugaam-close-button-hover-color);
  background-color: var(--ugaam-close-button-hover-background);
}
.ugaam-ad-wrapper-outer :is(button.ugaam-ad-close-button):is(:focus) {
  outline: none;
}
.ugaam-ad-wrapper-outer :is(button.ugaam-ad-close-button) svg {
  display: inline-flex;
  line-height: 1;
  width: 16px;
  height: 16px;
}

@media (min-width: 768px) {
  .ugaam-mobile-adsense-ad {
    display: none;
  }
}
@media (max-width: 768px) {
  .ugaam-desktop-tablet-adsense-ad {
    display: none;
  }
}
.ugaam-ad-wrapper-outer:not(.ugaam-sticky-ad) {
  padding-top: 30px;
  padding-bottom: 30px;
}

.ugaam-ad-wrapper-outer.ugaam-sticky-ad {
  display: flex;
  position: fixed;
  height: auto;
  width: fit-content;
  max-width: 100%;
  z-index: 10000000000000;
  transition: all 0.8s ease;
  /**
  *
  * Header sticky ad.
  *
  * @since 1.2.0
  */
  /**
  *
  * Footer sticky ad.
  *
  * @since 1.2.0
  */
  /**
  *
  * Alignment of sticky ads.
  *
  * @since 1.2.0
  */
}
.ugaam-ad-wrapper-outer.ugaam-sticky-ad :is(button.ugaam-ad-close-button) {
  display: inline-flex;
  opacity: 1;
  visibility: visible;
  backface-visibility: visible;
}
.ugaam-ad-wrapper-outer.ugaam-sticky-ad.is-sticky-header-ad {
  top: 0;
  bottom: auto;
}
.ugaam-ad-wrapper-outer.ugaam-sticky-ad.is-sticky-header-ad[data_display-ad=false] {
  top: -100%;
}
.ugaam-ad-wrapper-outer.ugaam-sticky-ad.is-sticky-header-ad[data_display-ad=true] {
  top: 0;
  animation: ugaamFadeInDown 0.8s ease;
}
.ugaam-ad-wrapper-outer.ugaam-sticky-ad.is-sticky-header-ad :is(a, img, iframe) {
  overflow: hidden;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
}
.ugaam-ad-wrapper-outer.ugaam-sticky-ad.is-sticky-header-ad :is(button.ugaam-ad-close-button) {
  margin-top: -3px;
  animation: ugaamFadeInDown 1.2s ease;
}
.ugaam-ad-wrapper-outer.ugaam-sticky-ad.is-sticky-footer-ad {
  top: auto;
  bottom: 0;
}
.ugaam-ad-wrapper-outer.ugaam-sticky-ad.is-sticky-footer-ad[data_display-ad=false] {
  bottom: -100%;
}
.ugaam-ad-wrapper-outer.ugaam-sticky-ad.is-sticky-footer-ad[data_display-ad=true] {
  animation: ugaamFadeInUp 0.8s ease;
}
.ugaam-ad-wrapper-outer.ugaam-sticky-ad.is-sticky-footer-ad :is(a, img, iframe) {
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.ugaam-ad-wrapper-outer.ugaam-sticky-ad.is-sticky-footer-ad :is(button.ugaam-ad-close-button) {
  margin-bottom: -3px;
  animation: ugaamFadeInUp 1.2s ease;
  border-radius: 0;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  box-shadow: 0 -2px 5px rgba(0, 0, 0, 0.1);
}
.ugaam-ad-wrapper-outer.ugaam-sticky-ad.is-sticky-footer-ad :is(button.ugaam-ad-close-button) svg {
  transform: rotate(180deg);
}
.ugaam-ad-wrapper-outer.ugaam-sticky-ad.is-sticky-footer-ad .ugaam-ad-wrapper-inner .ugaam-custom-ad {
  flex-direction: column;
}
.ugaam-ad-wrapper-outer.ugaam-sticky-ad:is(.is-left-align) {
  left: 0;
  right: auto;
  width: fit-content;
}
.ugaam-ad-wrapper-outer.ugaam-sticky-ad:is(.is-right-align) {
  right: 0;
  left: auto;
  width: fit-content;
}
.ugaam-ad-wrapper-outer.ugaam-sticky-ad:is(.is-center-align) {
  left: 0;
  right: 0;
  margin: 0 auto;
  width: fit-content;
  justify-content: center;
}

@keyframes ugaamFadeInDown {
  0% {
    opacity: 0;
    transform: translateY(-50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes ugaamFadeInUp {
  0% {
    opacity: 0;
    transform: translateY(50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes ugaamSlideFromLeft {
  0% {
    opacity: 0;
    transform: translateX(-50px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}
@keyframes ugaamSlideFromRight {
  0% {
    opacity: 0;
    transform: translateX(50px);
  }
  100% {
    opacity: 1;
    transform: translateX(0);
  }
}